import * as React from 'react';
import RcTree, { TreeNode } from 'rc-tree';
import PropTypes from 'prop-types';
import { withStyles } from '@material-ui/core/styles';
import styles from './styles';
import cssAnimation from 'css-animation';
const STYLE = `
.collapse {
  overflow: hidden;
  display: block;
}

.collapse-active {
  transition: height 0.2s ease-out;
}
`;

function animate(node, show, done) {
  let height = node.offsetHeight;
  return cssAnimation(node, 'collapse', {
    start() {
      if (!show) {
        node.style.height = `${node.offsetHeight}px`;
      } else {
        height = node.offsetHeight;
        node.style.height = 0;
      }
    },
    active() {
      node.style.height = `${show ? height : 0}px`;
    },
    end() {
      node.style.height = '';
      done();
    },
  });
}

const animation = {
  enter(node, done) {
    return animate(node, true, done);
  },
  leave(node, done) {
    return animate(node, false, done);
  },
  appear(node, done) {
    return animate(node, true, done);
  },
};

class Tree extends React.Component {
  static TreeNode = TreeNode;

  static defaultProps = {
    showLine: false,
    showIcon: false,
    multiple: false,
    checkable: false,
    disabled: false,
    checkStrictly: false,
    draggable: false,
    defaultExpandParent: true,
    autoExpandParent: false,
    defaultExpandAll: false,
    defaultExpandedKeys: [],
    defaultCheckedKeys: [],
    defaultSelectedKeys: [],
    prefixCls: 'rm-tree',
  };

  render() {
    const props = this.props;
    const { prefixCls, className } = props;
    let checkable = props.checkable;
    return (
      <RcTree
        {...props}
        className={className}
        checkable={checkable ? <span className={`${prefixCls}-checkbox-inner`} /> : checkable}
      >
        {/*<style dangerouslySetInnerHTML={{ __html: STYLE }}/>*/}
        {this.props.children}
      </RcTree>
    );
  }
}
Tree.propTypes = {
  /**
   * @ignore
   */
  prefixCls: PropTypes.string,
  /**
   * 
   */
  autoExpandParent: PropTypes.bool,
  /**
   *  Checkbox 
   */
  checkable: PropTypes.bool,
  /**
   * 
   * keykeycheckablecheckStrictlycheckedhalfChecked
   */
  checkedKeys: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.string),
    PropTypes.shape({
      checked: PropTypes.arrayOf(PropTypes.string),
      halfChecked: PropTypes.arrayOf(PropTypes.string),
    }),
  ]),
  /**
   * checkable
   */
  checkStrictly: PropTypes.bool,
  /**
   * 
   */
  defaultCheckedKeys: PropTypes.arrayOf(PropTypes.string),
  /**
   * 
   */
  defaultExpandAll: PropTypes.bool,
  /**
   * 
   */
  defaultExpandedKeys: PropTypes.arrayOf(PropTypes.string),
  /**
   * 
   */
  defaultExpandParent: PropTypes.bool,
  /**
   * 
   */
  defaultSelectedKeys: PropTypes.arrayOf(PropTypes.string),
  /**
   * 
   */
  disabled: PropTypes.bool,
  /**
   * IE>8
   */
  draggable: PropTypes.bool,
  /**
   * 
   */
  expandedKeys: PropTypes.arrayOf(PropTypes.string),
  /**
   * 
   */
  multiple: PropTypes.bool,
  /**
   * 
   */
  selectedKeys: PropTypes.arrayOf(PropTypes.string),
  /**
   *  TreeNode title  true
   */
  showIcon: PropTypes.bool,
  /**
   * 
   */
  showLine: PropTypes.bool,
  /**
   * 
   */
  onCheck: (checkedKeys, e) => null,
  /**
   * dragenter 
   */
  onDragEnter: options => null,
  /**
   * dragleave 
   */
  onDragLeave: options => null,
  /**
   * dragover 
   */
  onDragOver: options => null,
  /**
   * 
   */
  onDragStart: options => null,
  /** drop  */
  onDrop: options => null,
  /**
   * /
   */
  onExpand: (expandedKeys, info) => null,
  /**
   * 
   */
  onSelect: (selectedKeys, e) => null,
  /**
   * 
   */
  onRightClick: options => null,
};
export default withStyles(styles, { name: 'RMTree' })(Tree);
