import React from 'react';
import Switch from '@material-ui/core/Switch';
import FormControlLabel from '@material-ui/core/FormControlLabel';
import FormGroup from '@material-ui/core/FormGroup';
import { withStyles } from '@material-ui/core/styles';
import ButtonBase from 'plugins/Button';
import { FlatNavBar } from 'plugins/NavBar';
const { Item: FlatNavItem, SubNavBar: FlatNavSub, ItemGroup: FlatNavGroup } = FlatNavBar;
const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  flex: {
    flex: 1,
  },
  logo: {
    padding: '5px 10px',
  },
  button: {
    height: '100%',
    padding: '0 20px',
    background: theme.palette.primary.main,
    color: theme.palette.primary.contrastText,
  },
});
function Logo(props) {
  const { className } = props;
  return <img className={className} height="100%" src="/static/logo.png" alt="" />;
}
function RightTools(props) {
  const { classes } = props;
  return <ButtonBase className={classes.button}>Login</ButtonBase>;
}
class NavBarDemo extends React.Component {
  state = {
    theme: 'dark',
    selectedKeys: [],
  };
  onChange = ({ key, keyPath }) => {
    this.setState({
      selectedKeys: keyPath,
    });
  };
  handleChange = (event, checked) => {
    this.setState({ theme: checked ? 'dark' : 'light' });
  };

  render() {
    const { classes } = this.props;
    const { theme } = this.state;
    return (
      <div className={classes.root}>
        <FormGroup>
          <FormControlLabel
            control={
              <Switch
                checked={theme === 'dark' ? true : false}
                onChange={this.handleChange}
                aria-label="ThemeSwitch"
              />
            }
            label={theme === 'dark' ? 'Dark' : 'Light'}
          />
        </FormGroup>
        <FlatNavBar
          leftTools={<Logo className={classes.logo} />}
          rightTools={<RightTools classes={classes} />}
          theme={theme}
          selectedKeys={this.state.selectedKeys}
          onClick={this.onChange}
        >
          <FlatNavSub title="">
            <FlatNavGroup title="">
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup title="">
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup title="">
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup title="">
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup title="">
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
          </FlatNavSub>
          <FlatNavSub title="">
            <FlatNavGroup title="">
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup title="">
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
          </FlatNavSub>
          <FlatNavSub title="">
            <FlatNavGroup title="">
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
          </FlatNavSub>
          <FlatNavSub title="">
            <FlatNavGroup>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup>
              <FlatNavItem></FlatNavItem>
            </FlatNavGroup>
            <FlatNavGroup>
              <FlatNavItem key={'123'}></FlatNavItem>
            </FlatNavGroup>
          </FlatNavSub>
        </FlatNavBar>
      </div>
    );
  }
}

export default withStyles(styles)(NavBarDemo);
