import React, { Component } from 'react';
import PropTypes from 'prop-types';
import {withStyles} from "@material-ui/core/styles";
import TextField from '../TextField';
import Paper from '@material-ui/core/Paper';
import Pagination from '../Pagination/Pagination';
import Divider from '@material-ui/core/Divider';
import MenuItem from '@material-ui/core/MenuItem';
import throttling from '../utils/throttling.js';
const styles = theme => ({
  root: {
    flexGrow: 1,
  },
  container: {
    flexGrow: 1,
    position: 'relative',
  },
  textarea: {
    width: '100%',
  },
  modal: {
    position: 'fixed',
    top: 0,
    right: 0,
    bottom: 0,
    left: 0,
    overflow: 'hidden',
    outline: 0,
    backgroundColor: 'rgb(0, 0, 0)',
    opacity: 0,
    zIndex: 1,
  },
  paper: {
    position: 'absolute',
    zIndex: 200,
    marginTop: theme.spacing.unit,
    left: 0,
    right: 0,
    padding: '10px',
  },
  chip: {
    margin: `${theme.spacing.unit / 2}px ${theme.spacing.unit / 4}px`,
  },
  inputRoot: {
    flexGrow: 1,
    flexWrap: 'wrap',
  },
  inputHold: {
    padding: '10px 0 7px',
  },
  notFound: {
    padding: '10px',
    color: 'rgba(0,0,0,0.5)',
  },
});
class Mention extends Component {
  static propTypes = {
    /**
     * Callback when input @ content changes,parameters are (value,trigger)
     */
    // inputChangeCb: PropTypes.func.isRequired,
    onSearchChange: PropTypes.func.isRequired,
    /**
     * Callback when input content changes
     */
    onChange: PropTypes.func.isRequired,

    /**
     * pagination config
     */
    pageConfig: PropTypes.shape({
      page: PropTypes.number,
      rowsPerPage: PropTypes.number,
      count: PropTypes.number,
    }),
    /**
     * text palcehold
     */
    placeholder: PropTypes.string,
    /**
     * callback when select value change
     */
    onSelect: PropTypes.func.isRequired,
    /**
     * default value
     */
    defaultValue: PropTypes.string,
    /**
     * should component disabled
     */
    disabled: PropTypes.bool,
    /**
     * set char of trigger
     */
    //triggerOptions: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    prefix: PropTypes.oneOfType([PropTypes.string, PropTypes.array]),
    /**
     * set the option to  be selected
     */
    dataSource: PropTypes.oneOfType([PropTypes.object, PropTypes.array]),
    /**
     * show error
     */
    showError: PropTypes.bool,
    /**
     * initial selected value
     */
    selected: PropTypes.array,
    /**
     * set if the text can only be read
     */
    readOnly: PropTypes.bool,
  };
  static defaultProps = {
    onSearchChange: function() {},
    onChange: function() {},
    onSelect: function() {},
    disabled: false,
    prefix: ['@'], //
    placeholder: 'please input something',
    readOnly: false,
    defaultValue: '',
    selected: [], //item
  };
  constructor(props) {
    super();
    this.state = {
      open: false,
      inputValue: props.defaultValue,
      selectedItem: props.selected,
      triggerOption: '',
      pageConfig: {
        page: props.pageConfig.page || 0,
        rowsPerPage: props.pageConfig.rowsPerPage || 5,
        count: props.pageConfig.count || 5,
      },
    };
  }
  handleChange = e => {
    let trigger = null;
    if (this.state.open == false) {
      //
      if (
        e.target.value && e.target.value.indexOf(' ') === -1 &&
        this.props.prefix.indexOf(e.target.value.charAt(0)) > -1
      ) {
        //
        trigger = e.target.value[0];
        this.setState({
          open: true,
          triggerOption: trigger,
        });
        this.props.onSearchChange(e.target.value.slice(1, e.target.value.length), trigger); //
      } else if (e.target.value.indexOf(' ') > -1) {
        //
        let stringForComplete = e.target.value.split(' ').pop();
        if (this.props.prefix.indexOf(stringForComplete[0]) > -1) {
          trigger = stringForComplete[0];
        }
        if (trigger) {
          this.setState({
            open: true,
            triggerOption: trigger,
          });
          this.props.onSearchChange(stringForComplete.slice(1, stringForComplete.length), trigger); //
        }
      }
    } else if (e.target.value) {
      //
      let targetOptionIndex = e.target.value.lastIndexOf(this.state.triggerOption);
      let searchLength = e.target.value.length;
      let filterOption = e.target.value.slice(targetOptionIndex + 1, searchLength);
      this.props.onSearchChange(filterOption, this.state.triggerOption);
    }
    this.setState({
      inputValue: e.target.value,
    });
    this.props.onChange(e.target.value); //
  };
  handleItemClick = item => e => {
    let position = this.state.inputValue.lastIndexOf(this.state.triggerOption); //
    let newValue = this.state.inputValue.slice(0, position + 1) + item; //item
    let newSelectedItem = [...this.state.selectedItem, item]; //
    this.setState({
      open: false,
      inputValue: newValue,
      selectedItem: newSelectedItem,
    });
    this.props.onSelect(newSelectedItem); //
    this.props.onChange(newValue); //
  };
  handleBlur(e) {
    this.setState({
      open: false,
    });
  }
  pageCallbackFn(i) {
    //
    this.setState({
      pageConfig: {
        ...this.state.pageConfig,
        page: i,
      },
    });
  }
  static getDerivedStateFromProps(newProps, prevState) {
    //state
    if (newProps !== prevState.preProps) {
      return {
        pageConfig: { ...prevState.pageConfig, count: newProps.pageConfig.count },
        prevProps: newProps,
      };
    }
    return null;
  }
  render() {
    const {
      classes,
      placeholder,
      children,
      dataSource,
      disabled,
      showError,
      pageConfig,
    } = this.props;
    const { count, page, rowsPerPage } = this.state.pageConfig;
    const { open, inputValue, selectedItem } = this.state;
    let items;
    let showPagination = false;
    let noItemPatterned = false;
    noItemPatterned = (dataSource && dataSource.length == 0) || (!dataSource && !React.children);
    if (pageConfig) {
      showPagination = true;
    }
    if (dataSource) {
      //dataSource
      items = dataSource
        ? dataSource.map(item => {
            let selected = false;
            if (typeof item === 'string') {
              if (!Array.isArray(selectedItem)) {
                throw new Error(
                  'React-Material: the `value` property must be an array ' +
                    'when using the `AutoComplete` component with `multiple`.',
                );
              }
              selected = selectedItem.indexOf(item) !== -1;
              return (
                <MenuItem
                  key={item}
                  value={item}
                  selected={selected}
                  onClick={this.handleItemClick(item)}
                >
                  {item}
                </MenuItem>
              );
            } else {
              throw new Error('AutoComplete[dataSource] only supports type `string[] | Object[]`.');
            }
          })
        : [];
    } else if (React.Children) {
      //child
      items = React.Children
        ? React.Children.map(children, child => {
            if (!React.isValidElement(child)) {
              return null;
            }
            let selected = false;
            if (!Array.isArray(selectedItem)) {
              throw new Error(
                'React-Material: the `value` property must be an array ' +
                  'when using the `AutoComplete` component with `multiple`.',
              );
            }
            selected = selectedItem.indexOf(child.props.value) !== -1;
            return React.cloneElement(child, {
              onClick: this.handleItemClick(child.props.value),
              role: 'option',
              selected,
              value: undefined, // The value is most likely not a valid HTML attribute.
              'data-value': child.props.value, // Instead, we provide it as a data attribute.
            });
          })
        : [];
    }
    return (
      <div className={classes.root}>
        {open ? <div onClick={this.handleBlur.bind(this)} className={classes.modal} /> : null}
        <div className={classes.container}>
          <TextField
            disabled={disabled}
            className={classes.textarea}
            onChange={throttling(this.handleChange).bind(this)}
            value={inputValue}
            placeholder={placeholder}
            error={showError}
          />
          {open &&
            !noItemPatterned && (
              <Paper className={classes.paper} square>
                {items.slice(
                  count == 0 ? count : page * rowsPerPage,
                  (page + 1) * rowsPerPage > count ? count : (page + 1) * rowsPerPage,
                )}
                <Divider />
                {showPagination && (
                  <Pagination
                    {...this.state.pageConfig}
                    onChangePage={this.pageCallbackFn.bind(this)}
                  />
                )}
              </Paper>
            )}
          {open &&
            noItemPatterned && (
              <Paper className={classes.notFound}>
                {'no patterned result, press space to end'}
              </Paper>
            )}
        </div>
      </div>
    );
  }
}

export default withStyles(styles, { name: 'RMMention' })(Mention);
