import { addValidationRule } from 'formsy-react';

// required
addValidationRule('isRequired', (values, value) => {
  return !(value === undefined || value === null || value === '');
});

// 
addValidationRule('min', (values, value, v) => {
  return value >= v;
});

// 
addValidationRule('max', (values, value, v) => {
  return value <= v;
});
