import React from 'react';
import {withStyles} from "@material-ui/core/styles";
import Tag from 'plugins/Tag';
import CodeAddr from '@components/CodeAddr/CodeAddr.jsx';
const styles = () => ({
  blockDiv: {
    marginTop: '10px',
  },
});

class App extends React.Component {
  onClose = key => {
    console.log(key);
  };

  render() {
    const { classes } = this.props;
    return (
      <div>
        <CodeAddr addr="Tag/Tag.jsx"/>
        <div className={classes.blockDiv}>
          <div>
            <span>basic tag</span>
          </div>

          <Tag closable={false} label="basic " />
          <Tag closable={true} label="" onClose={this.onClose.bind(this)} />
        </div>

        <div className={classes.blockDiv}>
          <div>
            <span>three colors</span>
          </div>

          <Tag
            color="primaryLight"
            closable={true}
            onClose={this.onClose.bind(this)}
            label="primaryLight"
          />

          <Tag
            color="secondaryLight"
            closable={false}
            onClose={this.onClose.bind(this)}
            label="secondaryLight"
          />

          <Tag
            color="errorLight"
            closable={true}
            onClose={this.onClose.bind(this)}
            label="errorLight"
          />
        </div>

        <div className={classes.blockDiv}>
          <div>
            <span>three sizes</span>
          </div>

          <Tag size="small" label="small" />

          <Tag size="medium" label="medium" />

          <Tag size="large" label="large" />
        </div>
      </div>
    );
  }
}
